.userslider {
  display: flex;
  align-items: center;
  margin: 20px;
 width: 90rem;
height: 40rem;
opacity: 0;
  transform: scale(55%);
  transition: 2s;
}

.slider__image {
  width: 35%;
}

.userslider--zoom{

 width: 90rem;
height: 40rem;
opacity: 1;
transform:scale(75%)  ;

}

.slider__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.slider__title {
  text-align: center;
  color: white;
}



.VendorButtonsR{
 --color: #fff2f6;
 --color2: #6f6f6f;
 padding: 0.8em 1.7em;
 background-color: lightblue;
 border-radius: 10px;
 border: .5px solid var(--color);
 transition: .5s ease;
 position: relative;
 overflow: hidden;
 cursor: pointer;
 z-index: 1;
 font-weight: 200;
 font-size: 12px;
 font-family: 'Roboto', 'Segoe UI', sans-serif;
 text-transform: uppercase;
 color: var(--color);
}

.VendorButtonsR::before {
 content: '';
 display: block;
 height: 110%;
 width: 310%;
 transform: translate(0, -50%);
 position: absolute;
 top: 50%;
 left: -5%;
 z-index: -1;
 transition: .7s;
 background: -moz-linear-gradient(45deg, var(--color2) 33%, var(--color) 66%);
 background: -webkit-linear-gradient(45deg, var(--color2) 33%, var(--color) 66%);
 background: linear-gradient(45deg, var(--color2) 33%, var(--color) 66%);
}

.VendorButtonsR:hover::before {
 left: -200%;
}

.VendorButtonsR:hover {
 color: var(--color2);
}

.VendorButtonsR:active {
 transform: scale(.98);
 filter: brightness(.8);
}







